<template>
  <v-container>
    <v-stepper alt-labels v-model="e1">
      <!-- START OF STEPPER HEADER-->
      <v-stepper-header>
        <v-stepper-step :complete="e1 > 1" class="pa-3" step="1">
          Indicator 1
        </v-stepper-step>

        <v-divider></v-divider>

        <v-stepper-step :complete="e1 > 2" class="pa-3" step="2">
          Indicator 2
        </v-stepper-step>

        <v-divider></v-divider>

        <v-stepper-step :complete="e1 > 3" class="pa-3" step="3">
          Indicator 3
        </v-stepper-step>

        <v-divider></v-divider>

        <v-stepper-step :complete="e1 > 4" class="pa-3" step="4">
          Indicator 4
        </v-stepper-step>

        <v-divider></v-divider>

        <v-stepper-step :complete="e1 > 5" class="pa-3" step="5">
          Indicator 5
        </v-stepper-step>

        <v-divider></v-divider>

        <v-stepper-step :complete="e1 > 6" class="pa-3" step="6">
          Indicator 6
        </v-stepper-step>

        <v-divider></v-divider>

        <v-stepper-step :complete="e1 > 7" class="pa-3" step="7">
          Indicator 7
        </v-stepper-step>
      </v-stepper-header>
      <!-- END OF STEPPER HEADER-->
      <v-btn class="mt-2 mx-2" color="primary" @click="dialogAdd = true"
        >Show Plan</v-btn
      >
      <!-- <v-btn class="mt-2 mx-2" color="primary" @click="print">Print</v-btn> -->
      <v-btn
        v-if="user_type == 4"
        class="mt-2 mx-2"
        color="primary"
        @click="getRegEvaluation"
        >Copy Region's Evaluation</v-btn
      >
      <!-- START OF STEPPER CONTENT-->
      <v-layout row wrap>
        <v-flex>
          <v-card id="printMe2" class="overflow-auto mt-7 mx-2">
            <v-stepper-items>
              <v-stepper-content step="1">
                <div class="text-h5">Final Disposal Site</div>
                <!-- START ROW 1 -->
                <v-row>
                  <v-col sm="6">
                    <div class="text-body-2">
                      Open/Controlled dumpsite ceased operation
                      <span class="red--text">*</span>
                    </div>
                    <v-radio-group
                      v-model="indicator[0].radio1"
                      row
                      class="ml-5"
                    >
                      <v-radio label="Yes" value="0"></v-radio>
                      <v-radio label="No" value="1"></v-radio>
                    </v-radio-group>
                  </v-col>
                  <v-col sm="6">
                    <div class="text-body-2">
                      Remarks
                      <span class="red--text">*</span>
                    </div>
                    <v-textarea
                      :value="indicator[0].remarks1"
                      @change="(v) => (indicator[0].remarks1 = v)"
                      outlined
                      dense
                    >
                    </v-textarea>
                  </v-col>
                </v-row>
                <hr />
                <!-- END ROW 1 -->
                <br />
                <!-- START ROW 2 -->
                <v-row>
                  <v-col sm="6">
                    <div class="text-body-2">
                      Safe Closure and Rehabilitation Submitted
                      <span class="red--text">*</span>
                    </div>
                    <v-radio-group
                      v-model="indicator[0].radio2"
                      row
                      class="ml-5"
                    >
                      <v-radio label="Yes" value="0"></v-radio>
                      <v-radio label="No" value="1"></v-radio>
                    </v-radio-group>
                  </v-col>
                  <v-col sm="6">
                    <div class="text-body-2">
                      Remarks
                      <span class="red--text">*</span>
                    </div>
                    <v-textarea
                      :value="indicator[0].remarks2"
                      @change="(v) => (indicator[0].remarks2 = v)"
                      outlined
                    >
                    </v-textarea>
                  </v-col>
                </v-row>
                <hr />
                <!-- END ROW 2 -->
                <br />
                <!-- START ROW 3 -->
                <v-row>
                  <v-col sm="6">
                    <div class="text-body-2">
                      Sanitary Landfill
                      <span class="red--text">*</span>
                    </div>
                    <v-radio-group
                      v-model="indicator[0].radio3"
                      row
                      class="ml-5"
                    >
                      <v-radio label="Yes" value="0"></v-radio>
                      <v-radio label="No" value="1"></v-radio>
                    </v-radio-group>
                    <br />
                    <div class="text-subtitle-2">
                      Memorandum of Agreement
                      <span class="red--text">*</span>
                    </div>
                    <v-radio-group
                      v-model="indicator[0].radio4"
                      row
                      class="ml-5"
                    >
                      <v-radio label="Yes" value="1"></v-radio>
                      <v-radio label="No" value="2"></v-radio>
                      <v-radio label="Not Applicable" value="3"></v-radio>
                    </v-radio-group>
                    <br />
                    <div class="text-subtitle-2">
                      Contract
                      <span class="red--text">*</span>
                    </div>
                    <v-radio-group
                      v-model="indicator[0].radio5"
                      row
                      class="ml-5"
                    >
                      <v-radio label="Yes" value="1"></v-radio>
                      <v-radio label="No" value="2"></v-radio>
                      <v-radio label="Not Applicable" value="3"></v-radio>
                    </v-radio-group>
                  </v-col>
                  <v-col sm="6">
                    <div class="text-body-2">
                      Remarks
                      <span class="red--text">*</span>
                    </div>
                    <v-textarea
                      :value="indicator[0].remarks3"
                      @change="(v) => (indicator[0].remarks3 = v)"
                      outlined
                    >
                    </v-textarea>
                  </v-col>
                </v-row>
                <hr />
                <!-- END ROW 3 -->
                <br />
                <!-- START ROW 4 -->
                <v-row>
                  <v-col sm="6">
                    <div class="text-body-2">
                      Residual Containment Area
                      <span class="red--text">*</span>
                    </div>
                    <v-radio-group
                      v-model="indicator[0].radio6"
                      row
                      class="ml-5"
                    >
                      <v-radio label="Yes" value="0"></v-radio>
                      <v-radio label="No" value="1"></v-radio>
                    </v-radio-group>
                    <div class="text-subtitle-2">Capacity</div>
                    <v-text-field
                      outlined
                      dense
                      v-model="indicator[0].capacity"
                    ></v-text-field>
                    <div class="text-subtitle-2">Location</div>
                    <v-text-field
                      outlined
                      dense
                      v-model="indicator[0].location"
                    ></v-text-field>
                    <br />
                    <div class="text-body-2">
                      Future disposal area (please check the appropriate box)
                      <span class="red--text">*</span>
                    </div>
                    <v-radio-group
                      v-model="indicator[0].radio7"
                      row
                      class="ml-5"
                    >
                      <v-radio label="Owned" value="0"></v-radio>
                      <v-radio label="Cluster" value="1"></v-radio>
                    </v-radio-group>
                    <div class="text-subtitle-2">Location</div>
                    <v-text-field
                      outlined
                      dense
                      v-model="indicator[0].fdLocation"
                    ></v-text-field>
                    <div class="text-subtitle-2">Life Span</div>
                    <v-text-field
                      outlined
                      dense
                      v-model="indicator[0].lifeSpan"
                    ></v-text-field>
                    <div class="text-subtitle-2">Status</div>
                    <v-text-field
                      outlined
                      dense
                      v-model="indicator[0].fdStatus"
                    ></v-text-field>
                  </v-col>
                  <v-col sm="6">
                    <div class="text-body-2">
                      Remarks
                      <span class="red--text">*</span>
                    </div>
                    <v-textarea
                      :value="indicator[0].remarks4"
                      @change="(v) => (indicator[0].remarks4 = v)"
                      outlined
                    >
                    </v-textarea>
                  </v-col>
                </v-row>
                <hr />
                <!-- END ROW 4 -->

                <v-card-actions>
                  <v-spacer></v-spacer>
                  <v-btn color="primary" @click="next()">Next</v-btn>
                </v-card-actions>
              </v-stepper-content>

              <v-stepper-content step="2">
                <div class="text-h5">Materials Recovery Facilities</div>
                <!-- ROW 1 -->
                <v-row>
                  <v-col sm="6">
                    <div class="text-body-2">
                      <span>*</span>
                      At the end of the planning period, all barangays should be
                      service by MRF
                    </div>
                    <div class="text-body-2">
                      <span>*</span>
                      If utilizing centralized MRF, define which brgys are
                      covered by the centralized MRF(e.g. 2 brgys/ 15 total
                      brgys)
                    </div>
                    <v-radio-group v-model="indicator[1].radio1" row>
                      <v-radio label="Yes" value="0"></v-radio>
                      <v-radio label="No" value="1"></v-radio>
                    </v-radio-group>
                  </v-col>
                  <v-col sm="6">
                    <div class="text-body-2">
                      Remarks
                      <span class="red--text">*</span>
                    </div>
                    <v-textarea
                      :value="indicator[1].remarks1"
                      @change="(v) => (indicator[1].remarks1 = v)"
                      outlined
                    >
                    </v-textarea>
                  </v-col>
                </v-row>
                <hr />
                <!-- ROW 1 -->
                <v-card-actions>
                  <v-btn @click="e1 = 1">Back </v-btn>
                  <v-spacer></v-spacer>
                  <v-btn color="primary" @click="next()">Next</v-btn>
                </v-card-actions>
              </v-stepper-content>

              <v-stepper-content step="3">
                <div class="text-h5">Diversion Targets</div>
                <!-- ROW 1 -->
                <v-row>
                  <v-col sm="6">
                    <div class="text-body-2">
                      Above 50% by 2017
                      <span class="red--text">*</span>
                    </div>
                    <v-radio-group v-model="indicator[2].radio1" row>
                      <v-radio label="Yes" value="0"></v-radio>
                      <v-radio label="No" value="1"></v-radio>
                    </v-radio-group>
                    <div class="text-body-2">
                      Increasing diversion target and decreasing disposal
                      targets
                      <span class="red--text">*</span>
                    </div>
                    <v-radio-group v-model="indicator[2].radio2" row>
                      <v-radio label="Yes" value="0"></v-radio>
                      <v-radio label="No" value="1"></v-radio>
                    </v-radio-group>
                  </v-col>
                  <v-col sm="6">
                    <div class="text-body-2">
                      Remarks
                      <span class="red--text">*</span>
                    </div>
                    <v-textarea
                      :value="indicator[2].remarks1"
                      @change="(v) => (indicator[2].remarks1 = v)"
                      outlined
                    >
                    </v-textarea>
                  </v-col>
                </v-row>
                <hr />
                <!-- ROW 1 -->
                <v-card-actions>
                  <v-btn @click="e1 = 2">Back </v-btn>
                  <v-spacer></v-spacer>
                  <v-btn color="primary" @click="e1 = 4">Next</v-btn>
                </v-card-actions>
              </v-stepper-content>

              <v-stepper-content step="4">
                <div class="text-h5">
                  Segregation at Source and ssegreagated Collection
                </div>
                <!-- ROW 1 -->
                <v-row>
                  <v-col sm="6">
                    <div class="text-body-2">
                      100% target for the !0th year planning period
                      <span class="red--text">*</span>
                    </div>
                    <v-radio-group v-model="indicator[3].radio1" row>
                      <v-radio label="Yes" value="0"></v-radio>
                      <v-radio label="No" value="1"></v-radio>
                    </v-radio-group>
                  </v-col>
                  <v-col sm="6">
                    <div class="text-body-2">
                      Remarks
                      <span class="red--text">*</span>
                    </div>
                    <v-textarea
                      :value="indicator[3].remarks1"
                      @change="(v) => (indicator[3].remarks1 = v)"
                      outlined
                    >
                    </v-textarea>
                  </v-col>
                </v-row>
                <hr />
                <!-- ROW 1 -->
                <v-card-actions>
                  <v-btn @click="e1 = 3">Back </v-btn>
                  <v-spacer></v-spacer>
                  <v-btn color="primary" @click="e1 = 5">Next</v-btn>
                </v-card-actions>
              </v-stepper-content>

              <v-stepper-content step="5">
                <div class="text-h5">Expenditures/ Investment</div>
                <!-- ROW 1 -->
                <v-row>
                  <v-col sm="6">
                    <div class="text-body-2">
                      Presence of Appropriate projected investment costs for
                      strategies identified
                      <span class="red--text">*</span>
                    </div>
                    <v-radio-group v-model="indicator[4].radio1" row>
                      <v-radio label="Yes" value="0"></v-radio>
                      <v-radio label="No" value="1"></v-radio>
                    </v-radio-group>
                  </v-col>
                  <v-col sm="6">
                    <div class="text-body-2">
                      Remarks
                      <span class="red--text">*</span>
                    </div>
                    <v-textarea
                      :value="indicator[4].remarks1"
                      @change="(v) => (indicator[4].remarks1 = v)"
                      outlined
                    >
                    </v-textarea>
                  </v-col>
                </v-row>
                <hr />
                <!-- ROW 1 -->
                <!-- ROW 2 -->
                <v-row>
                  <v-col sm="6">
                    <div class="text-body-2">
                      Source of fund is indicated
                      <span class="red--text">*</span>
                    </div>
                    <v-radio-group v-model="indicator[4].radio2" row>
                      <v-radio label="Yes" value="0"></v-radio>
                      <v-radio label="No" value="1"></v-radio>
                    </v-radio-group>
                  </v-col>
                  <v-col sm="6">
                    <div class="text-body-2">
                      Remarks
                      <span class="red--text">*</span>
                    </div>
                    <v-textarea
                      :value="indicator[4].remarks2"
                      @change="(v) => (indicator[4].remarks2 = v)"
                      outlined
                    >
                    </v-textarea>
                  </v-col>
                </v-row>
                <hr />
                <!-- ROW 2 -->
                <v-card-actions>
                  <v-btn @click="e1 = 4">Back </v-btn>
                  <v-spacer></v-spacer>
                  <v-btn color="primary" @click="next()">Next</v-btn>
                </v-card-actions>
              </v-stepper-content>

              <v-stepper-content step="6">
                <div class="text-h5">Presence of Policy Support</div>
                <!-- ROW 1 -->
                <v-row>
                  <v-col sm="6">
                    <v-radio-group v-model="indicator[5].radio1" row>
                      <v-radio label="Yes" value="0"></v-radio>
                      <v-radio label="No" value="1"></v-radio>
                    </v-radio-group>
                  </v-col>
                  <v-col sm="6">
                    <div class="text-body-2">
                      Remarks
                      <span class="red--text">*</span>
                    </div>
                    <v-textarea
                      :value="indicator[5].remarks1"
                      @change="(v) => (indicator[5].remarks1 = v)"
                      outlined
                    >
                    </v-textarea>
                  </v-col>
                </v-row>
                <hr />
                <!-- ROW 1 -->
                <v-card-actions>
                  <v-btn @click="e1 = 5">Back </v-btn>
                  <v-spacer></v-spacer>
                  <v-btn color="primary" @click="next()">Next</v-btn>
                </v-card-actions>
              </v-stepper-content>

              <v-stepper-content step="7">
                <div class="text-h5">
                  Presence of institutional/organizational arrangements
                </div>
                <!-- ROW 1 -->
                <v-row>
                  <v-col sm="6">
                    <v-radio-group v-model="indicator[6].radio1" row>
                      <v-radio label="Yes" value="0"></v-radio>
                      <v-radio label="No" value="1"></v-radio>
                    </v-radio-group>
                  </v-col>
                  <v-col sm="6">
                    <div class="text-body-2">
                      Remarks
                      <span class="red--text">*</span>
                    </div>
                    <v-textarea
                      :value="indicator[6].remarks1"
                      @change="(v) => (indicator[6].remarks1 = v)"
                      outlined
                    >
                    </v-textarea>
                  </v-col>
                </v-row>
                <hr />
                <!-- ROW 1 -->
                <v-card-actions>
                  <v-btn @click="e1 = 6">Back </v-btn>
                  <v-spacer></v-spacer>
                  <v-btn color="primary" @click="save()">Save</v-btn>
                </v-card-actions>
              </v-stepper-content>
            </v-stepper-items>
          </v-card>
        </v-flex>
      </v-layout>
      <!-- END OF STEPPER CONTENT-->
    </v-stepper>

    <template>
      <v-dialog v-model="dialogAdd" max-width="1300px" :retain-focus="false"
        ><v-card>
          <v-card-title>
            <span class="headline"></span>
          </v-card-title>
          <v-card-text>
            <v-container
              v-for="(chapterContent, chapterIndex) in chapters"
              :key="chapterIndex"
              :step="chapterIndex + 1"
              class="mt-10"
            >
              <!-- :id="'print-chapter-' + (chapterIndex + 1)" -->
              <div>
                <!-- PRINT LOOP FOR CHAPTER TITLE-->
                <div
                  class="text-h4 text-center"
                  v-text="chapterContent.chapterTitle"
                ></div>
                <!-- PRINT LOOP FOR CHAPTER TITLE-->
                <br />

                <!-- PRINT LOOP CHAPTER SUB TITLE-->
                <div
                  class="text-h5 text-center"
                  v-text="chapterContent.chapterSubTitle"
                ></div>
                <!-- PRINT LOOP CHAPTER SUB TITLE-->
                <br />

                <!--START LOOP OF OUTLINE PER CHAPTER-->
                <v-container
                  v-for="(outline, outlineIndex) in chapterContent.outline"
                  :key="outlineIndex"
                >
                  <p class="text-h6">{{ outline.outlineTitle }}</p>
                  <p>{{ outline.outlineModel }}</p>

                  <!--START ATTACHMENT PER OUTLINE-->
                  <v-container
                    class="not-to-print"
                    v-for="(upload, uploadIndex) in outline.uploads"
                    :key="'up' + uploadIndex"
                  >
                    <v-row id="not-to-print" class="not-to-print">
                      <v-col md="4">
                        <v-menu offset-y>
                          <template v-slot:activator="{ on, attrs }">
                            <v-btn
                              color="primary"
                              dark
                              v-bind="attrs"
                              v-on="on"
                            >
                              Attachments
                            </v-btn>
                          </template>
                          <v-list>
                            <!-- :href="item.new_name" -->
                            <template v-for="(item, index) in upload.curr_data">
                              <v-list-item
                                :key="`fle` + index"
                                v-if="item.ext == `pdf`"
                                :href="item.new_name"
                                target="_blank"
                                :download="item.new_name"
                              >
                                <v-list-item-title>
                                  {{ item.orig_name }}</v-list-item-title
                                >
                              </v-list-item>
                            </template>
                          </v-list>
                        </v-menu>
                      </v-col>
                      <!-- <div class="p" v-text="upload.curr_data"></div> -->
                    </v-row>

                    <v-container>
                      <v-row>
                        <template v-for="(item, index) in upload.curr_data">
                          <v-col
                            :key="`img` + index"
                            v-if="item.ext == `jpg`"
                            class="d-flex child-flex"
                            cols="4"
                          >
                            <v-img
                              :src="item.new_name"
                              max-width="300"
                              class="grey lighten-2"
                            >
                              <template v-slot:placeholder>
                                <v-row
                                  class="fill-height ma-0"
                                  align="center"
                                  justify="center"
                                >
                                  <v-progress-circular
                                    indeterminate
                                    color="grey lighten-5"
                                  ></v-progress-circular>
                                </v-row>
                              </template>
                            </v-img>
                          </v-col>
                        </template>
                      </v-row>
                    </v-container>
                  </v-container>
                  <!--START ATTACHMENT PER OUTLINE-->

                  <!-- PRINT TEXTAREA OF OUTLINE PER CHAPTER-->

                  <!--START LOOP OF TABLE PER OUTLINE-->
                  <v-container
                    v-for="(table, tableIndex) in outline.outlineTable"
                    :key="tableIndex"
                  >
                    <br />
                    <!--START SET UP OF TABLE WITH LOOP FOR HEADERS AND TABLE DATA-->
                    <template v-if="table.tableName == '821'">
                      <v-data-table
                        :headers="table.tableHeader"
                        :items="table.tableData"
                        :hide-default-footer="true"
                        class="elevation-1"
                        style="white-space: pre-line"
                        dense
                      >
                        <template v-slot:top>
                          <v-toolbar flat>
                            <v-toolbar-title class="text-subtitle-2">{{
                              table.tableTitle
                            }}</v-toolbar-title>
                            <v-divider class="mx-4" inset vertical></v-divider>
                            <v-spacer></v-spacer>
                            <template v-if="table.tableAddNew > 0">
                              <v-btn
                                color="primary"
                                dark
                                class="mb-2"
                                @click="
                                  addItem(
                                    chapterIndex,
                                    outlineIndex,
                                    tableIndex,
                                    table.tableName
                                  )
                                "
                              >
                                New Item
                              </v-btn>
                            </template>
                          </v-toolbar>
                        </template>
                        <!-- <template v-slot:header="props">
                  <thead>
                    <tr>
                      <th colspan="2">Category 1</th>
                      <th colspan="3">Category 2</th>
                    </tr>
                  </thead>
                </template> -->
                        <!--START OF CREATING ACTIONS PER ROW-->
                        <template v-slot:[`item.actions`]="{ item }">
                          <v-icon
                            small
                            class="mr-2"
                            @click="
                              editItem(
                                chapterIndex,
                                outlineIndex,
                                tableIndex,
                                table.tableName,
                                item
                              )
                            "
                          >
                            mdi-pencil
                          </v-icon>
                          <v-icon
                            small
                            @click="
                              deleteItem(
                                chapterIndex,
                                outlineIndex,
                                tableIndex,
                                item
                              )
                            "
                            v-if="table.tableAddNew > 0"
                          >
                            mdi-delete
                          </v-icon>
                        </template>
                        <!--END OF CREATING ACTIONS PER ROW-->
                        <!-- <template slot="body.append">
                <tr v-html="footerTotal()" class="pink--text"></tr>
              </template> -->
                      </v-data-table>
                    </template>

                    <template v-else>
                      <v-data-table
                        dense
                        :headers="filterHeader(table.tableHeader)"
                        :items="table.tableData"
                        :items-per-page="-1"
                        :hide-default-footer="true"
                        class="elevation-1"
                        style="white-space: pre-line"
                        loading="false"
                        loading-text="No Data"
                      >
                        <!-- v-if="table.tableName == '231'" -->
                        <template v-slot:top>
                          <v-toolbar flat>
                            <v-toolbar-title class="text-subtitle-2">{{
                              table.tableTitle
                            }}</v-toolbar-title>
                            <v-divider class="mx-4" inset vertical></v-divider>
                            <v-spacer></v-spacer>
                          </v-toolbar>
                        </template>
                        <template
                          slot="body.append"
                          v-if="table.withTotal == 1"
                        >
                          <tr>
                            <td class="text-subtitle-2">Total</td>
                            <td
                              class="text-subtitle-2"
                              v-for="x in nonNullItems(
                                chapterIndex,
                                outlineIndex,
                                tableIndex
                              )"
                              :key="x"
                              :id="`${tableIndex}-${x}`"
                            >
                              {{
                                sumField(
                                  chapterIndex,
                                  outlineIndex,
                                  tableIndex,
                                  x
