<template>
  <v-container>
    <v-tabs v-model="tab" align-with-title>
      <v-tabs-slider color="yellow"></v-tabs-slider>

      <v-tab @click="changeTab(1)"> New Application </v-tab>
      <v-tab @click="changeTab(0)"> Approved Application </v-tab>
    </v-tabs>

    <v-tabs-items v-model="tab">
      <v-tab-item>
        <EMBPlanList />
      </v-tab-item>
      <v-tab-item>
        <PlanlistApproved />
      </v-tab-item>
    </v-tabs-items>
  </v-container>
</template>

<script>
import EMBPlanList from "../components/Eplan/EmbPlanList";
import PlanlistApproved from "../components/Eplan/PlanlistApproved";
import "../sass/create_plan_style.scss";
import { mapGetters, mapActions } from "vuex";
export default {
  name: "Eplan",
  data() {
    return { tab: null, limitRow: [10] };
  },
  created() {
    this.fetchPlanList({
      offset: this.tgetCurrentrow,
      limit: this.limitRow,
      status: 1,
      new_app: 1,
    });
  },
  methods: {
    ...mapActions(["fetchPlanList"]),
    changeTab(index) {
      this.fetchPlanList({
        offset: this.tgetCurrentrow,
        limit: this.limitRow,
        status: 1,
        new_app: index,
      });
    },
  },
  components: {
    EMBPlanList,
    PlanlistApproved,
  },
  computed: {
    ...mapGetters(["tgetCurrentrow"]),
  },
};
</script>