<template>
  <v-container>
    <v-toolbar color="cyan" dark>
      <v-toolbar-title>USER MANAGEMENT</v-toolbar-title>
    </v-toolbar>
    <v-card class="mx-auto card-padding">
      <template>
        <v-dialog v-model="dialogAdd" persistent max-width="600px">
          <template v-slot:activator="{ on, attrs }">
            <v-row>
              <v-col>
                <v-row>
                  <v-col>
                    <v-text-field
                      label="Search Bar"
                      v-model="userSearch"
                      @keyup="getSearch"
                    ></v-text-field>
                  </v-col>
                  <v-col>
                    <v-combobox
                      v-model="limitRow"
                      label="Show"
                      :items="allLimit"
                      @change="limitFunction"
                    >
                    </v-combobox>
                  </v-col>
                </v-row>
                <v-btn color="primary" dark v-bind="attrs" v-on="on">
                  Add New
                </v-btn>
              </v-col>
              <v-col>
                <!-- <v-btn @click="getSearch">Search</v-btn> -->
                <v-btn
                  class="float-right mini-margin-left"
                  @click="nextFunction"
                  >Next</v-btn
                >
                <v-btn class="float-right" @click="prevFunction">Prev</v-btn>
                <input v-model="getCurrenrow" type="hidden" />
              </v-col>
            </v-row>
          </template>
          <v-card>
            <v-card-title>
              <span class="headline">Add New User</span>
            </v-card-title>
            <v-card-text>
              <v-container>
                <v-row>
                  <v-col cols="6">
                    <v-text-field
                      label="Search by First or Last Name"
                      v-model="iisemployee"
                      required
                    >
                    </v-text-field>
                  </v-col>
                  <v-col cols="1">
                    <v-btn
                      @click="getEmployee"
                      icon
                      style="margin: 20px 0px 0px 0px !important"
                    >
                      <v-icon>mdi-magnify</v-icon>
                    </v-btn>
                  </v-col>
                  <v-col cols="5">
                    <v-switch
                      flat
                      @change="switch1 = 1 - switch1"
                      :label="`${switch1 == 1 ? 'LGU' : 'Evaluator'}`"
                    ></v-switch>
                  </v-col>
                  <v-col cols="12">
                    <v-simple-table class="mini-margin-top">
                      <template v-slot:default>
                        <thead>
                          <tr>
                            <th>Selection</th>
                            <th>Employee Number</th>
                            <th>Name</th>
                            <th>Region</th>
                          </tr>
                        </thead>
                        <tbody>
                          <tr v-for="iis in allIisEmployee" :key="iis.id">
                            <td>
                              <v-btn
                                color="info"
                                depressed
                                x-small
                                @click="selectIISEmp(iis)"
                                fab
                              >
                                <v-icon>mdi-selection-multiple</v-icon>
                              </v-btn>
                            </td>
                            <td>{{ iis.id }}</td>
                            <td>{{ iis.name }}</td>
                            <td>{{ iis.region }}</td>
                          </tr>
                        </tbody>
                      </template>
                    </v-simple-table>
                  </v-col>
                  <v-col cols="6">
                    <v-text-field
                      label="Employee Number *"
                      v-model="iis_employee_number"
                      required
                      readonly
                    >
                    </v-text-field>
                  </v-col>
                  <v-col cols="6">
                    <v-text-field
                      label="Full Name *"
                      v-model="name"
                      required
                      readonly
                    >
                    </v-text-field>
                  </v-col>
                  <v-col cols="12">
                    <v-combobox
                      :items="allAccess"
                      v-model="user_access_level"
                      label="Responsibility"
                      outlined
                      dense
                    >
                    </v-combobox>
                    <v-select
                      v-if="switch1 == 0"
                      :items="adminStatus"
                      v-model="switch2"
                      label="Administrator"
                      outlined
                      dense
                    >
                      <option
                        v-for="option in adminStatus"
                        v-bind:key="option.value"
                      >
                        {{ option.text }}
                      </option>
                    </v-select>
                  </v-col>
                </v-row>
              </v-container>
              <small>*indicates required field</small>
            </v-card-text>
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn color="blue darken-1" text @click="dialogAdd = false"
                >Close</v-btn
              >
              <v-btn color="blue darken-1" text @click="insertUser">Save</v-btn>
            </v-card-actions>
          </v-card>
        </v-dialog>
      </template>

      <!-- Modal Update Product -->
      <template>
        <v-dialog v-model="dialogEdit" persistent max-width="600px">
          <v-card>
            <v-card-title>
              <span class="headline">Update User Details</span>
            </v-card-title>
            <v-card-text>
              <v-container>
                <v-row>
                  <v-col cols="12">
                    <v-text-field
                      label="Employee Number *"
                      v-model="iis_employee_number_edit"
                      required
                      readonly
                    >
                    </v-text-field>
                  </v-col>
                  <v-col cols="12">
                    <v-text-field
                      label="Full Name *"
                      v-model="name_edit"
                      required
                      readonly
                    >
                    </v-text-field>
                  </v-col>
                  <v-col cols="6">
                    <v-select
                      :items="allAccess"
                      v-model="user_access_level_edit"
                      label="Responsibility"
                      outlined
                      dense
                    >
                      <option
                        v-for="option in allAccess"
                        v-bind:key="option.value"
                      >
                        {{ option.text }}
                      </option>
                    </v-select>
                  </v-col>
                  <v-col cols="6">
                    <v-select
                      :items="itemStatus"
                      v-model="active"
                      label="Status"
                      outlined
                      dense
                    >
                      <option
                        v-for="option in itemStatus"
                        v-bind:key="option.value"
                      >
                        {{ option.text }}
                      </option>
                    </v-select>
                  </v-col>
                  <v-col cols="6">
                    <v-select
                      v-if="switch1 == 0"
                      :items="adminStatus"
                      v-model="switch2"
                      label="Administrator"
                      outlined
                      dense
                    >
                      <option
                        v-for="option in adminStatus"
                        v-bind:key="option.value"
                      >
                        {{ option.text }}
                      </option>
                    </v-select>
                  </v-col>
                </v-row>
              </v-container>
              <!-- <small>*indicates required field</small> -->
            </v-card-text>
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn color="blue darken-1" text @click="dialogEdit = false"
                >Close</v-btn
              >
              <v-btn color="blue darken-1" text @click="updateUser"
                >Update</v-btn
              >
            </v-card-actions>
          </v-card>
        </v-dialog>
      </template>

      <!-- Modal Delete Product -->
      <template>
        <v-dialog v-model="dialogDelete" persistent max-width="600px">
          <v-card>
            <v-card-title>
              <span class="headline"></span>
            </v-card-title>
            <v-card-text>
              <v-container>
                <v-row>
                  <h2>Are sure want to delete ?</h2>
                </v-row>
              </v-container>
            </v-card-text>
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn color="blue darken-1" text @click="dialogDelete = false"
                >No</v-btn
              >
              <v-btn color="info darken-1" text @click="deleteUser">Yes</v-btn>
            </v-card-actions>
          </v-card>
        </v-dialog>
      </template>

      <template>
        <v-simple-table class="mini-margin-top">
          <template v-slot:default>
            <thead>
              <tr>
                <th>Actions</th>
                <th>#</th>
                <th>Name</th>
                <th>Employee Number</th>
                <th>Access Level</th>
                <th>Administrator</th>
                <th>Origin</th>
                <th>Status</th>
              </tr>
            </thead>
            <tbody>
              <tr v-for="user in allUsers" :key="user.id">
                <td>
                  <v-btn
                    color="info"
                    depressed
                    x-small
                    @click="getEdit(user)"
                    fab
                  >
                    <v-icon>mdi-pencil</v-icon>
                  </v-btn>
                  |
                  <v-btn
                    color="error"
                    depressed
                    fab
                    x-small
                    @click="getDelete(user)"
                  >
                    <v-icon>mdi-delete-alert-outline</v-icon>
                  </v-btn>
                </td>
                <td>{{ user.id }}</td>
                <td>{{ user.name }}</td>
                <td>{{ user.iis_employee_number }}</td>
                <td>{{ user.access_level }}</td>
                <td v-if="user.admin === '0'">
                  <v-chip link color="error" class="ma-2 ml-0">
                    <v-icon>mdi-close-circle</v-icon>
                  </v-chip>
                </td>
                <td v-else-if="user.admin == '1'">
                  <v-chip link color="success" class="ma-2 ml-0">
                    <v-icon>mdi-checkbox-marked-circle</v-icon>
                  </v-chip>
                </td>
                <td>{{ user.origin == 1 ? "CRS" : "IIS" }}</td>
                <td>{{ user.active == 1 ? "Active" : "Inactive" }}</td>
              </tr>
            </tbody>
          </template>
        </v-simple-table>
      </template>
    </v-card>
  </v-container>
</template>
<script>
import { mapGetters, mapActions } from "vuex";

export default {
  name: "UserManagement",
  data() {
    return {
      dialogAdd: false,
      dialogEdit: false,
      dialogDelete: false,
      user_access_level: "",
      iis_employee_number: "",
      region_id: "",
      name: "",
      switch1: 0,
      switch2: 0,
      active: 0,
      itemStatus: [
        {
          value: 0,
          text: "Inactive",
        },
        {
          value: 1,
          text: "Active",
        },
      ],
      adminStatus: [
        {
          value: 1,
          text: "Yes",
        },
        {
          value: 0,
          text: "No",
        },
      ],
      userSearch: "",
      userID: "",
      user_access_level_edit: "",
      iis_employee_number_edit: "",
      name_edit: "",
      allLimit: [5, 10, 20, 50, 100],
      limitRow: [5],
      iisemployee: "",
    };
  },
  methods: {
    ...mapActions([
      "fetchUsers",
      "createUser",
      "increment",
      "decrement",
      "searchUsers",
      "updateUsers",
      "deleteUsers",
      "fetchAccess",
      "getIssEmployee",
    ]),
    selectIISEmp(iis) {
      this.iis_employee_number = iis.id;
      this.name = iis.name;
      this.region_id = iis.regid;
    },
    insertUser() {
      this.createUser({
        id: null,
        iis_employee_number: this.iis_employee_number,
        name: this.name,
        region: this.region_id,
        admin: this.switch2,
        user_access_level: this.user_access_level,
        offset: this.getCurrenrow,
        limit: this.limitRow,
      });
      this.iis_employee_number = "";
      this.name = "";
      this.user_access_level = "";
      // this.dialogAdd = false;
      //  this.fetchUsers({
      //   offset: this.getCurrenrow,
      //   limit: this.limitRow
      // });
    },
    nextFunction() {
      this.increment({
        offset: this.getCurrenrow,
        limit: this.limitRow,
      });
      this.fetchUsers({
        offset: this.getCurrenrow,
        limit: this.limitRow,
      });
    },
    prevFunction() {
      this.decrement({
        offset: this.getCurrenrow,
        limit: this.limitRow,
      });
      this.fetchUsers({
        offset: this.getCurrenrow,
        limit: this.limitRow,
      });
    },
    getSearch() {
      this.searchUsers({
        txtsearch: this.userSearch,
        limit: this.limitRow,
      });
    },
    getEdit(user) {
      this.iis_employee_number_edit = user.iis_employee_number;
      this.userID = user.id;
      this.active = parseInt(user.active);
      this.name_edit = user.name;
      this.switch1 = user.origin;
      this.switch2 = parseInt(user.admin);
      this.user_access_level_edit = user.user_access_level;
      this.dialogEdit = true;
    },
    updateUser() {
      this.updateUsers({
        id: this.userID,
        iis_employee_number: this.iis_employee_number_edit,
        name: this.name_edit,
        admin: this.switch2,
        active: this.active,
        user_access_level: this.user_access_level_edit,
        offset: this.getCurrenrow,
        limit: this.limitRow,
      });
    },
    getDelete(user) {
      this.userID = user.id;
      this.dialogDelete = true;
    },
    deleteUser() {
      this.deleteUsers({
        id: this.userID,
        offset: this.getCurrenrow,
        limit: this.limitRow,
      });
      this.dialogDelete = false;
    },
    limitFunction() {
      this.fetchUsers({
        offset: this.getCurrenrow,
        limit: this.limitRow,
      });
    },
    getEmployee() {
      this.getIssEmployee({ name: this.iisemployee, origin: this.switch1 });
    },
  },
  computed: mapGetters([
    "allUsers",
    "getCurrenrow",
    "allAccess",
    "allIisEmployee",
  ]),
  created() {
    this.fetchUsers({
      offset: this.getCurrenrow,
      limit: this.limitRow,
    });
    this.fetchAccess();
  },
};
</script>